/*
<--!----------------------------------------------------------------!-->
<--! COMMON SCSS !-->
<--!----------------------------------------------------------------!-->
*/
.main-wrapper {
  width: 100%;
  height: 100%;
}
.main-wrapper .cb-without-asidebar .cb-content {
  margin-left: var(--cb-navigation-width);
}
@media (max-width: 991.98px) {
  .main-wrapper .cb-without-asidebar .cb-content,
  .main-wrapper .cb-without-asidebar .cb-main-header {
    margin-left: 0;
  }
}

/*
<--!----------------------------------------------------------------!-->
<--! NAVIGATION SCSS !-->
<--!----------------------------------------------------------------!-->
*/
body .cb-navigation {
  width: var(--cb-navigation-width);
  min-width: var(--cb-navigation-width);
  transition: all 0.2s ease-in-out;
}
body .cb-navigation .cb-navigation-brand,
body .cb-navigation .cb-navigation-footer {
  width: var(--cb-navigation-width);
  min-width: var(--cb-navigation-width);
  height: var(--cb-navigation-brand-height);
  min-height: var(--cb-navigation-brand-height);
}
body .cb-navigation .cb-navigation-body .nav-item {
  margin-bottom: 0.5rem;
}
body .cb-navigation .cb-navigation-body .nav-item:last-child {
  margin-bottom: 0;
}
body .cb-navigation .cb-navigation-body .nav-item .nav-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem 0.5rem;
  font-size: 1.25rem;
  border-radius: 50rem;
  transition: all 0.2s ease-in-out;
}
body .cb-navigation .cb-navigation-body .nav-item .nav-link:hover {
  color: var(--cb-primary);
  background-color: var(--cb-link-bg);
  transition: all 0.2s ease-in-out;
}
body .cb-navigation .cb-navigation-body .nav-item .nav-link .fi {
  line-height: 0;
  font-size: 1.25rem;
}
body .cb-navigation .cb-navigation-footer div.nav-link {
  width: 3rem;
  height: 3rem;
  padding: 0.5rem 0.5rem;
  transition: all 0.2s ease-in-out;
}
body .cb-navigation .cb-navigation-footer div.nav-link:hover {
  color: var(--cb-primary);
  background-color: var(--cb-link-bg);
  transition: all 0.2s ease-in-out;
}
body .cb-navigation .cb-navigation-footer div.nav-link .fi {
  line-height: 0;
  font-size: 1.25rem;
}
@media (max-width: 991.98px) {
  body .cb-navigation {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}
@media (max-width: 991.98px) {
  body.cb-navigation-open .cb-navigation {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

/*
<--!----------------------------------------------------------------!-->
<--! MAIN HEADER SCSS !-->
<--!----------------------------------------------------------------!-->
*/
.cb-main-header {
  height: var(--cb-header-height);
  transition: all 0.2s ease-in-out;
}
.cb-main-header .cb-asidebar-search-wrapper .cb-asidebar-search {
  height: var(--cb-header-height);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
}
.cb-main-header .cb-asidebar-search-wrapper .cb-asidebar-search.search-form-open {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s ease-in-out;
}
.cb-main-header .btn-icon .fi {
  line-height: 0;
  font-size: 1.25rem;
}
.cb-main-header .cb-main-header-right .cb-icon {
  line-height: 0;
}
.cb-main-header .cb-main-header-right .cb-notifications-item .cb-notifications-action {
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
}
.cb-main-header .cb-main-header-right .cb-notifications-item:hover .cb-notifications-action {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s ease-in-out;
}
.cb-main-header .cb-main-header-right .cb-theme-applications .dropdown-menu a {
  border-radius: 0.313rem;
  transition: all 0.2s ease-in-out;
}
.cb-main-header .cb-main-header-right .cb-theme-applications .dropdown-menu a:hover {
  background-color: var(--cb-link-bg);
}

/*
<--!----------------------------------------------------------------!-->
<--! ASIDE MAIN SCSS !-->
<--!----------------------------------------------------------------!-->
*/
body .cb-asidebar {
  background: var(--cb-body-bg);
  width: min(var(--cb-asidebar-width), 100%);
  transition: all 0.2s ease-in-out;
}
@media (min-width: 1200px) {
  body .cb-asidebar {
    margin-left: var(--cb-navigation-width);
  }
}
@media (max-width: 1199.98px) {
  body .cb-asidebar {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}
body .cb-asidebar.cb-asidebar-md {
  width: min(var(--cb-asidebar-width) - 5rem, 100%);
}
body .cb-asidebar.cb-asidebar-md ~ .cb-content {
  margin-left: calc(var(--cb-navigation-width) + var(--cb-asidebar-width) - 5rem);
}
body .cb-asidebar.cb-asidebar-without-sub-header .cb-asidebar-body {
  height: calc(100% - 10rem);
}
body .cb-asidebar .cb-asidebar-header,
body .cb-asidebar .cb-asidebar-sub-header,
body .cb-asidebar .cb-asidebar-footer {
  height: var(--cb-header-height);
}
body .cb-asidebar .cb-asidebar-body {
  height: calc(100% - 15rem);
}
body .cb-asidebar .cb-asidebar-body .nav-link.active {
  background-color: var(--cb-link-bg);
  border-radius: 0.313rem;
}
@media (min-width: 992px) {
  body.cb-asidebar-open .cb-asidebar {
    margin-left: var(--cb-navigation-width);
  }
}
@media (max-width: 1199.98px) {
  body.cb-asidebar-open .cb-asidebar {
    opacity: 1;
    visibility: visible;
    transform: translateX(0%);
  }
}

/*
<--!----------------------------------------------------------------!-->
<--! Content SCSS !-->
<--!----------------------------------------------------------------!-->
*/
@media (max-width: 991.98px) {
  body .cb-asidebar.cb-asidebar-md ~ .cb-content {
    margin-left: 0 !important;
  }
}
@media (max-width: 1199.98px) {
  body .cb-asidebar.cb-asidebar-md ~ .cb-content {
    margin-left: var(--cb-navigation-width);
  }
}
body .cb-content {
  min-height: calc(100vh - var(--cb-header-height));
  margin-left: calc(var(--cb-navigation-width) + var(--cb-asidebar-width));
  transition: all 0.2s ease-in-out;
}
body .cb-content .cb-content-header {
  height: var(--cb-header-height);
}
body .cb-content .cb-content-body {
  padding: 1.5rem 1.5rem;
  transition: all 0.2s ease-in-out;
}
body .cb-content .cb-content-footer {
  height: var(--cb-header-height);
}
@media (max-width: 991.98px) {
  body .cb-content {
    margin-left: 0 !important;
  }
}
@media (max-width: 1199.98px) {
  body .cb-content {
    margin-left: var(--cb-navigation-width);
  }
}
body.header-search-open .cb-content .cb-content-header .cb-content-header-search-wrapper .cb-header-search {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s ease-in-out;
}
body.cb-without-asidebar .cb-content {
  margin-left: var(--cb-navigation-width);
}

/*
<--!----------------------------------------------------------------!-->
<--! OFFCANVAS ASIDEBAR SCSS !-->
<--!----------------------------------------------------------------!-->
*/
body .offcanvas .cb-offcanvas-header,
body .offcanvas .cb-offcanvas-footer {
  height: var(--cb-header-height);
}
body .offcanvas .cb-offcanvas-body {
  height: calc(100% - 10rem);
}
@media (min-width: 1200px) and (max-width: 1599.98px) {
  body.offcanvas-opened .main-wrapper .cb-asidebar {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}
body.offcanvas-opened .main-wrapper .cb-content {
  margin-right: var(--cb-asidebar-width);
}
