  opacity: 1;
  visibility: visible;
}
.cb-content .cb-content-body .cb-chat-wrapper .cb-chat-items .cb-chat-item:last-child:after {
  right: 100%;
  content: "";
  height: 0;
  width: 0;
  bottom: 1rem;
  position: absolute;
