  color: var(--cb-dark);
  background-color: var(--cb-link-bg);
}

.nav-tabs-flush .nav-item.show .nav-link {
  color: var(--cb-primary);
  border-color: var(--cb-primary);
}
.nav-tabs-flush .nav-item .nav-link {
  border-top-width: 0;
  border-left-width: 0;
  border-right-width: 0;
  border-bottom: var(--cb-nav-tabs-border-width) solid transparent;
}
.nav-tabs-flush .nav-item .nav-link.active {
  color: var(--cb-primary);
  border-color: var(--cb-primary);
}

.nav-tabs-select .nav-item .nav-link.active {
  border-color: var(--cb-primary) !important;
}

.nav-tabs-xline {
  position: relative;
}
.nav-tabs-xline:before, .nav-tabs-xline:after {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 0.0625rem;
  bottom: -0.0625rem;
  background: var(--cb-border-color);
}
.nav-tabs-xline:before {
  left: -1.5rem;
}
.nav-tabs-xline:after {
  right: -1.5rem;
}

