  animation-duration: 500ms;
  animation-name: fade-in-down;
}
@keyframes fade-in-down {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/*
<--!----------------------------------------------------------------!-->
<--! MISCELLANEOUS SCSS !-->
<--!----------------------------------------------------------------!-->
*/
.fi {
  line-height: 0;
  font-size: 1rem;
}

.cb-gradient-bg {
  background-image: radial-gradient(circle, #ffffff, #f4f6fb, #e7eef6, #d7e7f0, #d8e9ee);
}

.bg-image-blur {
  filter: blur(5px);
}

.tooltip {
  font-weight: 700;
}

dl.dl-horizontal {
  display: grid;
  grid-template-columns: 9.375rem auto;
}
dl.dl-horizontal dt {
  grid-column-start: 1;
  margin-bottom: 1.25rem;
  color: var(--cb-dark);
  font-weight: 600;
}
dl.dl-horizontal dd {
  grid-column-start: 2;
  margin-bottom: 1.25rem;
}

.typing .wave .dot {
  display: inline-block;
  width: 0.3125rem;
  height: 0.3125rem;
  border-radius: 50rem;
  background-color: #ADB5BD;
  animation: wave 1.3s linear infinite;
}
.typing .wave .dot:first-of-type {
  margin-left: 0.1875rem;
}
.typing .wave .dot:nth-child(2) {
  animation-delay: -1.1s;
  margin-left: 0.125rem;
}
.typing .wave .dot:nth-child(3) {
  animation-delay: -0.9s;
  margin-left: 0.125rem;
}
@keyframes wave {
  0%, 100%, 60% {
    transform: initial;
  }
  30% {
    transform: translateY(-0.3125rem);
  }
}

.upload-button {
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.upload-button:hover {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s ease-in-out;
  background: var(--cb-tertiary-bg);
}

::-webkit-scrollbar {
  width: 0.375rem;
  height: 0.375rem;
  border-radius: 1rem;
}

::-webkit-scrollbar-track {
  background-color: var(--cb-bg-body-secondary);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--cb-gray-300);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--cb-dark);
  transition: all 0.2s ease-in-out;
}

