}
.ps:hover > .ps__rail-x {
  opacity: 0.6;
}
.ps:hover > .ps__rail-y {
  opacity: 0.6;
}
.ps .ps__rail-x.ps--clicking {
  opacity: 0.9;
  background-color: #DEE2E6;
}
.ps .ps__rail-x:focus {
  opacity: 0.9;
  background-color: #DEE2E6;
}
.ps .ps__rail-x:hover {
  opacity: 0.9;
  background-color: #DEE2E6;
}
.ps .ps__rail-y.ps--clicking {
  opacity: 0.9;
  background-color: #DEE2E6;
}
.ps .ps__rail-y:focus {
  opacity: 0.9;
  background-color: #DEE2E6;
}
.ps .ps__rail-y:hover {
  opacity: 0.9;
  background-color: #DEE2E6;
}

.ps__rail-x {
  z-index: 10;
  opacity: 0;
  bottom: 0;
  height: 1rem;
  display: none;
  position: absolute;
  transition: background-color 0.2s linear, opacity 0.2s linear;
}
.ps__rail-x:focus > .ps__thumb-x {
  height: 0.625rem;
  background-color: #212529;
}
.ps__rail-x:hover > .ps__thumb-x {
  height: 0.625rem;
